
.html-content {
  max-width: 800;
  padding: 0px 10px 10px 10px;
  white-space: initial;
  overflow: hidden;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
  line-height: 29px;
  font-size: 16px;
}
.html-content img {
  max-width: 90%;  
  height: auto !important
}
.html-content * {
  padding: revert;
  margin: revert;
  box-sizing: revert;
}
.html-content table p {
  margin: 0px;
}